            ...this.mobileItems[colIndex],
            expanded: !this.mobileItems[colIndex].expanded
          })
        }
      }
    },
    mounted() {
      let resultList = []
      this.pcItems.map((item) => {
        return item.map((col) => {resultList.push({...col, expanded: false})})
      })
      this.mobileItems = resultList
    }
  }
</script>

